$wine: #ba3660;
.bg-about {
  background-color: #f4ecef;
}
.btn {
  background-color: $wine;
  border: 1px solid $wine;
  box-shadow: 0 0 49px 10px rgba(186, 54, 96, .27), 0 3px 0 #75213c;

  &_submit {
    background-color: $wine;
  }
}
.container .owl-carousel .owl-nav button.owl-prev:before {
  background: url("../resources/images/icons/left-arrow-wine.png") no-repeat;
}
.container  .owl-carousel .owl-nav button.owl-next,
.container .owl-carousel .owl-nav button.owl-prev {
  border: 1px solid $wine;
}

.container .owl-carousel .owl-nav button.owl-next {
  background-image: linear-gradient(-120deg, rgb(186,54,96) 0%, rgb(123,34,63) 100%);
}
.owl_dots .active{
  & span {
    background-color: $wine;
  }

  & span:after {
    border: 1px solid $wine;
  }
}
.icon_svg {
  fill: $wine;
}
